* {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}
a {
  display: block;
  text-decoration: none;
  color: #000;
}
img {
  display: block;
  width: 100%;
}
// font
@font-face {
  font-family: "KOTRA_GOTHIC";
  src: url(../font/KOTRA_BOLD_.woff) format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Bookk Myungjo";
  src: url(../font/부크크명조_굵은글씨.woff);
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Bookk Myungjo";
  src: url(../font/부크크명조_얇은글씨.woff);
  font-weight: 400;
  font-style: normal;
}

// mixin
$main: #fcf9ee;
$sub: #000;
$border: (0.3px solid #000);
@mixin ko {
  font-family: "KOTRA_GOTHIC";
  font-weight: normal;
  font-style: normal;
}
@mixin en {
  font-family: "Bookk Myungjo";
  font-weight: 400;
  font-style: normal;
}
@mixin en_b {
  font-family: "Bookk Myungjo";
  font-weight: 700;
  font-style: bold;
}
@mixin size($width, $height) {
  width: $width;
  height: $height;
}
@mixin text($height) {
  text-align: center;
  line-height: $height;
}

body {
  background-color: $main;
  position: relative;
  @include size(100%, 100%);
}
///body
h3 {
  @include en();
} //title en
.banner {
  @include size(100%, 40px);
  background-color: #000;
  display: flex;
  overflow: hidden;
  p {
    width: 100%;
    color: #fff;
    //animation: rolling 45s infinite both;
    line-height: 40px;
  }
  .close {
    display: block;
    @include size(30px, 30px);
    position: absolute;
    top: 0;
    right: -25%;
    color: #fff;
  }
}

@keyframes rolling {
  0% {
    transform: translateX(100%);
  }

  100% {
    transform: translateX(-500%);
  }
}
///rolling banner

//visual_main
.visual {
  @include size(100%, 100vh);
  position: relative;
  margin-bottom: 200px;
  video {
    @include size(100%, auto);
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
  }
}

//bg
//header
.header_outer {
  border: 1px solid #fff;
  border-right: none;
  border-left: none;
  box-sizing: border-box;
}
.header {
  @include size(1300px, 80px);
  @include en();
  display: flex;
  margin: 0 auto;
  justify-content: space-between;
  color: #fff;
  .logo {
    @include size(200px, auto);
    padding-top: 10px;
    margin-left: 45px;
    p {
      color: #fff;
      text-align: center;
      font-size: 30px;
      letter-spacing: 5px;
    }
    p:last-child {
      font-size: 12px;
      letter-spacing: 5px;
    }
  }
  .menu {
    margin-left: 45px;
    @include size(570px, 100%);
    ul {
      @include size(100%, 100%);
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      li {
        margin-right: 80px;
        cursor: pointer;
      }
    }
  }
  .gnb {
    display: flex;
    margin-right: 45px;

    ul {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 140px;
      li {
        @include size(30px, 30px);
        cursor: pointer;
        img {
          @include size(100%, 100%);
        }
      }
      li:first-child img {
        width: 80%;
        height: 80%;
        padding-top: 4px;
        padding-left: 4px;
      }
      li:last-child {
        margin-right: 0;
        img {
          /*!! 
           width: 120%;
          height: 120%; */
        }
      }
    }
  }
}
//header

//new
.new {
  @include size(1300px, 600px);
  margin: 0 auto;
  margin-bottom: 200px;
  h3 {
    margin-bottom: 10px;
    margin-left: 10px;
  }
  .new_inner {
    display: flex;
    .swiper {
      @include size(50%, 80%);
    }
    .right_box {
      width: 50%;
    }
  }
}
//new

//collection
.collection {
  @include size(1300px, 600px);
  margin: 0 auto;
  margin-bottom: 200px;
  h3 {
    margin-left: 10px;
  }
  .collection_inner {
    @include size(100%, 100%);
    background-image: url(../images/main/collection_01.jpg);
    background-size: 100% 100%;
    display: flex;
    .collection_box1 {
      background-color: rgba(0, 0, 0, 0.4);
    }
    .collection_box1:hover {
      background-color: rgba(0, 0, 0, 0);
      transition: 0.3s;
    }
    .collection_box2 {
      background-color: rgba(0, 0, 0, 0.4);
    }
    .collection_box2:hover {
      background-color: rgba(0, 0, 0, 0);
      transition: 0.3s;
    }
    .collection_box3 {
      background-color: rgba(0, 0, 0, 0.4);
    }
    .collection_box3:hover {
      background-color: rgba(0, 0, 0, 0);
      transition: 0.3s;
    }
    .collection_box4 {
      background-color: rgba(0, 0, 0, 0.4);
    }
    .collection_box4:hover {
      background-color: rgba(0, 0, 0, 0);
      transition: 0.3s;
    }
  }
  .collection_inner > div {
    @include size(25%, 100%);
    border: 0.5px solid #ccc;
    box-sizing: border-box;
  }
}
//collection

//best
.best {
  @include size(1300px, auto);
  margin: 0 auto;
  margin-bottom: 200px;
  h3 {
    margin-left: 10px;
  }
  .best_inner {
    display: flex;
    .best_box1 {
      width: 40%;
    }
    .best_box2 {
      width: 60%;
      display: flex;
      flex-wrap: wrap;
    }
    .best_box2 > div {
      width: calc(100% / 3);
      text-align: center;
      line-height: 30px;
    }
  }
}
//best

//test
.test {
  @include size(1300px, 600px);
  margin: 0 auto;
  margin-bottom: 200px;
  overflow: hidden;
}
.test > div {
  @include size(100%, 100%);
  border: 0.5px solid #000;
  box-sizing: border-box;
  text-align: center;
}
